@import url(https://fonts.googleapis.com/css2?family=Raleway:ital,wght@1,100;1,200&display=swap);
* {
  font-family: 'Raleway', sans-serif;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

ul{
  list-style-type: none;
}

a {
  color: black;
  text-decoration: none;
}

body {
  font-size: 1.2rem;
  line-height: 1.3;
  font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif
}

.navbar_navbar__1kGjF {
  width: 100%;
  height: 70px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-bottom: #333 solid 1px;
  padding: 1rem;
}

.navbar_logo__1u_az {
  display: flex;
  justify-content: center;
  margin-top: 20px;
}

.navbar_menu__25ewV {
  display: flex;
  align-items: center;
}

.navbar_menu__25ewV li {
  padding: 1rem;
}

.navbar_navbar__1kGjF a {
  color: #000000;
  font-size: medium;
  letter-spacing: 5px;
}

.navbar_navbar__1kGjF a:hover {
  color: #749246;
}

.navbar_mobile_btn__2HuuV {
  display: none;
}

@media screen and (max-width: 720px) {
  .navbar_mobile_btn__2HuuV {
    display: block;
    position: absolute;
    right: 1rem;
    cursor: pointer;
    z-index: 10;
  }

  .navbar_menu__25ewV {
    display: flex;
    flex-direction: column;
    justify-content: center;
    position: fixed;
    left: -100%;
    top: 0;
    bottom: 0;
    right: 0;
    width: 100%;
    height: 100vh;
    background-color: #D1E5EB;
    z-index: 10;
    transition: left 1s;
  }

  .navbar_active__3ngMU {
    left: 0;
  }

  .navbar_navbar__1kGjF a {
    font-size: 1.5rem;
  }
}
